import React from 'react';

interface MainStepProps {
  step: number;
  currentStep: number;
}

export default function MainStep({ step, currentStep }: MainStepProps) {
  if (step !== currentStep) {
    return null;
  }
  const solutionsList: Array<string> = [
    `Automate and Optimize Corporate Tax Filing`,
    `Business Accounting`,
    `Automate Payments`,
    `Formulate B2B Contracts`,
    `Incorporate Company`,
    `Formulate Investment Agreements`,
    `Establish Shareholder Agreements`,
    `Get Funding`,
    `Access Business Banking Services`,
    `Discover Markets and Convert Prospects`,
    `Scale Business Globally`,
  ];

  const solutionsCheckboxes = solutionsList.map((name, index) => (
    <div className="mt-2">
      <label
        className="inline-flex items-center"
        htmlFor={`field-solutions-${index}`}
        key={name}
      >
        <input
          id={`field-solutions-${index}`}
          type="checkbox"
          name="solutions"
          className="
      rounded
      bg-gray-200
      border-transparent
      focus:border-transparent focus:bg-gray-200
      text-gray-700
      focus:ring-1 focus:ring-offset-2 focus:ring-gray-500
    "
        />
        <span className="ml-2">{name}</span>
      </label>
    </div>
  ));

  return (
    <>
      <label className="block" htmlFor="email">
        <span className="text-gray-700">Email address</span>
        <span className="text-rose-500">*</span>
        <input
          type="email"
          id="email"
          className="
              mt-1
              block
              w-full
              rounded-md
              bg-gray-100
              border-transparent
              focus:border-gray-500 focus:bg-white focus:ring-0
              border-2
              border-rose-500
            "
          placeholder="john@example.com"
        />
        <span className="text-sm text-rose-500">
          Please fill out this field
        </span>
      </label>
      <div className="block">
        <span className="text-gray-700">
          What solutions are you looking for?
        </span>
        <span className="text-rose-500">*</span>
        {solutionsCheckboxes}
      </div>
    </>
  );
}
